import React from 'react';
import Modal from 'react-modal';
import { useParams, useHistory } from 'react-router-dom';

import evidenceFromJson from '@jan6evidence/data/evidence.json';

import { Evidence } from '@jan6evidence/types';

import type { Filters } from '../types/Filters';

import EvidenceDetail from './EvidenceDetail';
import { EvidenceMap, EvidenceTable, EvidenceTimeline } from './EvidenceMain';
import FilterBox from './FilterBox';
import { applyFilters, buildFilterChoices } from './FilterBox/filterUtils';
import { EvidenceResultsWrapper, EvidenceTableWrapper } from './table-styled-components';

// Dates are serailized as strings, so we need to manually deserialize
// Would be nice to move this deserializaiton to ../data/index.ts but we tried
// and ran into compile errors, so we're doing it here for now.
const deserDateStr = (str: string | undefined): Date | undefined => {
  if (!str) {
    return undefined;
  }
  try {
    return new Date(str);
  } catch {
    return undefined;
  }
};

const evidence: Evidence[] = evidenceFromJson.map((evItem) => {
  // We don't yet have any examples of `realTimeEnd` being populated, so this makes
  // the typer happy
  const evItemWithAllFields = { realTimeEnd: undefined, ...evItem };
  return {
    ...evItemWithAllFields,
    realTimeStart: deserDateStr(evItemWithAllFields.realTimeStart),
    realTimeEnd: deserDateStr(evItemWithAllFields.realTimeEnd),
  };
});

const FILTER_CHOICES = buildFilterChoices(evidence);

const EvidenceApp = () => {
  const [filters, setFilters] = React.useState<Filters>({
    location: new Set<string>(),
    suspect: new Set<string>(),
    otherTags: new Set<string>(),
  });
  const [keywords, setKeywords] = React.useState<string[]>([]);
  const { detailId } = useParams<{ detailId?: string }>();
  const history = useHistory();
  const closeModal = React.useCallback(() => history.push(`/evidence`), [history]);

  const results = applyFilters(evidence, filters, keywords);

  return (
    <div>
      <FilterBox
        onFilterChanged={(section, newContent) => {
          setFilters((oldFilters) => ({ ...oldFilters, [section]: newContent }));
        }}
        onKeywordsChanged={(newKeywords) => setKeywords(newKeywords)}
        filters={filters}
        choices={FILTER_CHOICES}
        keywords={keywords}
      />
      <EvidenceResultsWrapper>
        <h1>Evidence Database</h1>
        <EvidenceMap evidence={results} />
        <EvidenceTimeline />
        <EvidenceTableWrapper>
          <EvidenceTable results={results} />
        </EvidenceTableWrapper>
      </EvidenceResultsWrapper>
      <Modal
        isOpen={!!detailId}
        onRequestClose={closeModal}
        contentLabel="Evidence Details"
        style={{ overlay: { zIndex: 1000 } }}
      >
        <EvidenceDetail evidence={results.find((r) => r._id === detailId)!} onClose={closeModal} />
      </Modal>
    </div>
  );
};

export default EvidenceApp;
