import React, { Component } from 'react';
import { Slider, Rail, Handles, Tracks, Ticks } from 'react-compound-slider';

import { Range, SliderRail, Handle, Track, Tick } from './TimelineHelpers';
import { TimelineWrapper } from './evidence-results-styled-components';

const sliderStyle = {
  position: 'relative',
  width: '100%',
};

const defaultValues: Range = [1250, 1400];

type EvidenceTimelineState = {
  domain: Range;
  values: Range;
  update: Range;
  reversed: boolean;
};

class Example extends Component {
  constructor(props: any) {
    super(props);
    this.state = {
      domain: [1100, 2000],
      values: defaultValues.slice(),
      update: defaultValues.slice(),
      reversed: false,
    } as EvidenceTimelineState;
  }

  onUpdate = (update: Range) => {
    this.setState({ update });
  };

  onChange = (values: Range) => {
    this.setState({ values });
  };

  setDomain = (domain: Range) => {
    this.setState({ domain });
  };

  render() {
    const { domain, values, update, reversed } = this.state as EvidenceTimelineState;

    return (
      <TimelineWrapper>
        <div>{`${values[0]} - ${values[1]}`}</div>
        <div>{`${update[0]} - ${update[1]}`}</div>
        <Slider
          mode={1}
          step={5}
          domain={domain}
          reversed={reversed}
          rootStyle={sliderStyle}
          onUpdate={this.onUpdate}
          onChange={this.onChange}
          values={values}
        >
          <Rail>{({ getRailProps }) => <SliderRail getRailProps={getRailProps} />}</Rail>
          <Handles>
            {({ handles, getHandleProps }) => (
              <div className="slider-handles">
                {handles.map((handle) => (
                  <Handle
                    key={handle.id}
                    handle={handle}
                    domain={domain}
                    getHandleProps={getHandleProps}
                  />
                ))}
              </div>
            )}
          </Handles>
          <Tracks left={false} right={false}>
            {({ tracks, getTrackProps }) => (
              <div className="slider-tracks">
                {tracks.map(({ id, source, target }) => (
                  <Track key={id} source={source} target={target} getTrackProps={getTrackProps} />
                ))}
              </div>
            )}
          </Tracks>
          <Ticks count={10}>
            {({ ticks }) => (
              <div className="slider-ticks">
                {ticks.map((tick) => (
                  <Tick key={tick.id} tick={tick} count={ticks.length} />
                ))}
              </div>
            )}
          </Ticks>
        </Slider>
      </TimelineWrapper>
    );
  }
}

export default Example;
