import React from 'react';

import type { Evidence } from '@jan6evidence/types';

type Props = {
  evidence: Evidence;
  onClose: () => void;
};

export default function EvidenceDetail({ evidence, onClose }: Props) {
  return (
    <>
      <h2>Evidence Detail</h2>
      <button type="button" onClick={onClose}>
        close
      </button>
      <pre>{JSON.stringify(evidence, null, 4)}</pre>
    </>
  );
}
